
import { ref, useContext, useFetch } from "@nuxtjs/composition-api";
const __sfc_main = {};

__sfc_main.setup = (__props, __ctx) => {
  const {
    $axios,
    params
  } = useContext();
  const data = ref({
    factura: {},
    negocio: {},
    receptor: {}
  });
  const tos = ref([`/factura/${params.value.id}/reclamar-puntos`, `/factura/${params.value.id}/convertir-a-factura-electronica`]);
  useFetch(async () => {
    try {
      data.value = await $axios.$post('/iccloud/v1/login/ver-documento-codigo', {
        codigo: params.value.id
      });
    } catch (e) {
      console.log(e);
    }
  });
  return {
    data,
    tos
  };
};

export default __sfc_main;
