
import { computed } from "@nuxtjs/composition-api"
import { CSSColor, CSSMargin } from "../useCSS"

export default {
  props: {
    color: { type: String, default: 'graylight' }
  },

  setup(props) {
    const CSS = computed(() => {
      return {
        root: {
          [CSSColor['bgColor_' + props.color]]: true,
          [CSSMargin['bottom_16']]: true,
          [CSSMargin['top_16']]: true
        }
      }
    })

    return {
      CSS
    }
  }
}

